	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import qiye from '@/views/qiye/list'
	import news from '@/views/news/list'
	import gangweileixing from '@/views/gangweileixing/list'
	import zhaopinxinxi from '@/views/zhaopinxinxi/list'
	import wodeshenqing from '@/views/wodeshenqing/list'
	import xuesheng from '@/views/xuesheng/list'
	import config from '@/views/config/list'
	import users from '@/views/users/list'
	import jianli from '@/views/jianli/list'
	import qiyeRegister from '@/views/qiye/register'
	import qiyeCenter from '@/views/qiye/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/qiyeCenter',
			name: '',
			component: qiyeCenter
		}
		,{
			path: '/qiye',
			name: '',
			component: qiye
		}
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/gangweileixing',
			name: '',
			component: gangweileixing
		}
		,{
			path: '/zhaopinxinxi',
			name: '',
			component: zhaopinxinxi
		}
		,{
			path: '/wodeshenqing',
			name: '',
			component: wodeshenqing
		}
		,{
			path: '/xuesheng',
			name: '',
			component: xuesheng
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/jianli',
			name: '',
			component: jianli
		}
		]
	},
	{
		path: '/qiyeRegister',
		name: '',
		component: qiyeRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
